/* LoadingAnimation.css */

/* Add styles for the container */
.loader-container {
  position: fixed; /* Make sure the container stays in view */
  top: 0;
  left: 0;
  width: 100vw; /* Full viewport width */
  height: 100vh; /* Full viewport height */
  display: flex; /* Flexbox for centering */
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
  background: rgba(255, 255, 255, 0.8); /* Optional: semi-transparent background */
}

.loader {
  width: 80px; /* Loader size */
  aspect-ratio: 1;
  position: relative;
}

.loader:before,
.loader:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px; /* Loader inner size */
  aspect-ratio: 1;
  background: #f3b7dc;
  animation:
    l1-1 2s infinite,
    l1-2 .5s infinite;
}

.loader:after {
  background: #FF3D7F;
  animation-delay: -1s, 0s;
}

@keyframes l1-1 {
  0%   {top:0; left:0}
  25%  {top:100%; left:0}
  50%  {top:100%; left:100%}
  75%  {top:0; left:100%}
  100% {top:0; left:0}
}

@keyframes l1-2 {
  80%, 100% {transform: rotate(0.5turn)}
}
